
import Cell from 'vux/src/components/cell/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import {mapState} from 'vuex'
export default {
name: 'app',
components: {
Cell,
Drawer,
Group,
Loading,
Tabbar,
TabbarItem,
ViewBox,
XHeader,

},
data() {
return {
drawerVisibility: false,

}
},
computed: {
...mapState({
isLoading: state => {
return state.isLoading
}
}),
showDrawerIcon() {
return this.$route.path === '/' || this.$route.path === '/component/drawer';
},
leftOptions() {
return {
showBack: this.$route.path !== '/'
}
},
rightOptions() {
return {
showMore: true
}
},
title() {
return '标题';
},
direction() {
return 'forward';
}
},
mounted() {

},
methods: {
hideDrawer() {
this.drawerVisibility = false;
},
toggleDrawer() {
this.drawerVisibility = !this.drawerVisibility;
},
onClickMore() {

},
toggleView(index) {
if (index == "1") {
this.$router.push({
name: 'Hello'
})
}
else if (index == "2") {
this.$router.push({
name: 'AddItem'
});
}
else if (index == "3") {
this.$router.push({
name: 'Records'
});
}
else {
this.$router.push({
name: 'ComingSoon'
});
}
}

}
}




