export default {
  name: 'profile',
  props: {
    user: {
      type: Object,
      default () {
        return {Name: 'Alex', Gender: 'Man', Age: 24}
      }
    }
  },
  methods: {
    lazyLoadTest () {
      this.$router.push({name: 'AsyncTest'})
    }
  },
  // data () {
  //   return {
  //     user: this.user
  //   }
  // },
  computed: {
    name () {
      return this.user.Name
    },
    gender () {
      return this.user.Gender
    },
    age () {
      return this.user.Age
    }
  }
}



